import React, {useEffect} from 'react';
import Menu from '../../components/menu/menu';
import Header from '../../components/header/header';
import { TweenMax, Power4, gsap, Expo } from 'gsap';
import './homepage.scss';

const HomePage = () => {
    useEffect(() => {
        gsap.to('.first', 1.5, {
            delay: .2,
            left: '-100%',
            ease: Expo.easeInOut
        })
        gsap.to('.second', 1.5, {
            delay: .4,
            left: '-100%',
            ease: Expo.easeInOut
        })
        gsap.to('.third', 1.5, {
            delay: .6,
            left: '-100%',
            ease: Expo.easeInOut
        })

        TweenMax.staggerFrom('.logo-container', 1, {
            delay: 1.4,
            opacity: 0,
            x: -20,
            ease: Power4.easeInOut
        },0.08);
        TweenMax.staggerFrom('.nav .nav-menu', 1, {
            delay: 1.4,
            opacity: 0,
            y: -20,
            ease: Power4.easeInOut
        },0.08);
    }, []);

    
    return(
        <div className='homepage'>
            <div className="overlay first"></div>
            <div className="overlay second"></div>
            <div className="overlay third"></div>
            <Header />
            <Menu />
        </div>
    );
}
export default HomePage;